"use client";

import { useRouter, useParams } from "next/navigation";
import { Box, Button, Paper, TextField, Typography, Grid, Switch, FormControlLabel } from "@mui/material";
import { useState, useEffect } from "react";

const EditUser = () => {
  const router = useRouter();
  const { id } = useParams();

  const [formData, setFormData] = useState({
    username: "",
    email: "",
    password_hash: "",
    full_name: "",
    role: "",
    is_active: true,
  });

  useEffect(() => {
    // Fetch user data by ID from API
    setFormData({
      username: "mockuser",
      email: "mock@example.com",
      password_hash: "",
      full_name: "Mock User",
      role: "Admin",
      is_active: true,
    });
  }, [id]);

  const handleChange = (field, value) => setFormData({ ...formData, [field]: value });

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log("Update User:", { id, ...formData });
    router.push("/dashboard/user");
  };

  return (
    <Box sx={{ p: 4, bgcolor: "#f5f5f5", minHeight: "100vh" }}>
      <Paper sx={{ maxWidth: 700, mx: "auto", p: 4, borderRadius: 2 }}>
        <Typography variant="h5" mb={2}>
          Edit User
        </Typography>
        <form onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={6}>
              <TextField label="Username" fullWidth value={formData.username} onChange={(e) => handleChange("username", e.target.value)} />
            </Grid>
            <Grid item xs={6}>
              <TextField label="Email" type="email" fullWidth value={formData.email} onChange={(e) => handleChange("email", e.target.value)} />
            </Grid>
            <Grid item xs={6}>
              <TextField label="Password" type="password" fullWidth value={formData.password_hash} onChange={(e) => handleChange("password_hash", e.target.value)} />
            </Grid>
            <Grid item xs={6}>
              <TextField label="Full Name" fullWidth value={formData.full_name} onChange={(e) => handleChange("full_name", e.target.value)} />
            </Grid>
            <Grid item xs={6}>
              <TextField label="Role" fullWidth value={formData.role} onChange={(e) => handleChange("role", e.target.value)} />
            </Grid>
            <Grid item xs={6}>
              <FormControlLabel
                control={<Switch checked={formData.is_active} onChange={(e) => handleChange("is_active", e.target.checked)} />}
                label="Active"
              />
            </Grid>
          </Grid>

          <Box sx={{ mt: 3, display: "flex", justifyContent: "space-between" }}>
            <Button variant="outlined" onClick={() => router.push("/dashboard/user")}>
              Back
            </Button>
            <Button variant="contained" type="submit" color="primary">
              Update
            </Button>
          </Box>
        </form>
      </Paper>
    </Box>
  );
};

export default EditUser;
